import React from 'react';
import {
  SafeAreaView,
  Image,
  StyleSheet,
  FlatList,
  View,
  Text,
  StatusBar,
  TouchableOpacity,
  Dimensions,
} from 'react-native';
import { NavigationContainer } from '@react-navigation/native';
import Butao from './Button';

const { width, height } = Dimensions.get('window');

const COLORS = { primary: '#282534', white: '#b152dd' };

const slides = [
  {
    id: '1',
    image: require('C:/Users/Eugenio Silva/3D Objects/App3/StickerSmash/assets/images/imagens.png'),
    title: 'Encontre seus itens perdidos com facilidade',
    subtitle: 'Alcance seus objetivos de encontrar itens',
  },
  {
    id: '2',
    image: require('C:/Users/Eugenio Silva/3D Objects/App3/StickerSmash/assets/images/image1.png'),
    title: 'Alcance seus objetivos de encontrar itens',
    subtitle: 'Alcance seus objetivos de encontrar itens',
  },
  {
    id: '3',
    image: require('C:/Users/Eugenio Silva/3D Objects/App3/StickerSmash/assets/images/image3.png'),
    title: 'Valorize suas posses encontrando-as facilmente',
    subtitle: `Alcance seus objetivos de encontrar itens`
  },
];

const Slide = ({ item }) => {
  return (
    <View style={{ flex: 2, alignContent:'center',flexDirection:'column'}} >
      <Image
        source={item?.image}
        style={{ marginTop: 20, height: '270px', width,resizeMode: 'contain' }}
      />
      {/* <View style={{flexWrap:'wrap', margin:2}} > */}
        <Text style={[styles.title,{textAlign: 'justify',margin:'auto '}]}>{item?.title}</Text>
        <Text style={[styles.subtitle ,{textAlign: 'justify',margin:'auto '}]}>{item?.subtitle}</Text>
      {/* </View> */}
    </View>
  );
};

const OnboardingScreen = ({ navigation }) => {
  const [currentSlideIndex, setCurrentSlideIndex] = React.useState(0);
  const ref = React.useRef();
  const updateCurrentSlideIndex = e => {
    const contentOffsetX = e.nativeEvent.contentOffset.x;
    const currentIndex = Math.round(contentOffsetX / width);
    setCurrentSlideIndex(currentIndex);
  };

  const goToNextSlide = () => {
    const nextSlideIndex = currentSlideIndex + 1;
    if (nextSlideIndex != slides.length) {
      const offset = nextSlideIndex * width;
      ref?.current.scrollToOffset({ offset });
      setCurrentSlideIndex(currentSlideIndex + 1);
    }
  };

  const skip = () => {
    const lastSlideIndex = slides.length - 1;
    const offset = lastSlideIndex * width;
    ref?.current.scrollToOffset({ offset });
    setCurrentSlideIndex(lastSlideIndex);
  };

  const Footer = () => {
    return (
      <View
        style={{
          height: height * 0.25,
          justifyContent: 'space-between',
          paddingHorizontal: 20,
        }}>
        {/* Indicator container */}
        <View
          style={{
            flexDirection: 'row',
            justifyContent: 'center',
            marginTop: 2,
          }}>
          {/* Render indicator */}
          {slides.map((_, index) => (
            <View
              key={index}
              style={[
                styles.indicator,
                currentSlideIndex == index && {
                  backgroundColor: COLORS.white,
                  width: 25,
                },
              ]}
            />
          ))}
        </View>

        {/* Render buttons */}
        <View style={{marginBottom: 10}}>
          {currentSlideIndex == slides.length - 1 ? (
            <View style={{height: 50}}>
              <TouchableOpacity
                style={styles.btn}
                onPress={() =>  console.log('Hello')}>
                <Text style={{fontWeight: 'bold', fontSize: 15}}>
                  Vamos começar
                </Text>
              </TouchableOpacity>
            </View>
          ) : (
            <View style={{flexDirection: 'row'}}>
              <TouchableOpacity
                activeOpacity={0.8}
                style={[
                  styles.btn,
                  {
                    borderColor: COLORS.white,
                    borderWidth: 1,
                    backgroundColor: 'transparent',
                  },
                ]}
                onPress={skip}>
                <Text
                  style={{
                    fontWeight: 'bold',
                    fontSize: 15,
                    color: COLORS.white,
                  }}>
                  Saltar
                </Text>
              </TouchableOpacity>
              <View style={{width: 15}} />
              <TouchableOpacity
                activeOpacity={0.8}
                onPress={goToNextSlide}
                style={styles.btn}>
                <Text
                  style={{
                    fontWeight: 'bold',
                    fontSize: 15,
                  }}>
                  Proximo
                </Text>
              </TouchableOpacity>
            </View>
          )}
        </View>
      </View>
    );
  };

  return (
    <SafeAreaView style={{ flex: 1, backgroundColor: COLORS.primary }}>
      <StatusBar backgroundColor={COLORS.primary} />
      <FlatList
        ref={ref}
        onMomentumScrollEnd={updateCurrentSlideIndex}
        contentContainerStyle={{ height: height * 0.75 }}
        showsHorizontalScrollIndicator={false}
        horizontal
        data={slides}
        pagingEnabled
        renderItem={({ item }) => <Slide item={item} />}
      />
      <Footer />
    </SafeAreaView>
  );
};

const styles = StyleSheet.create({
  subtitle: {

    color: '#fff',
    fontSize: 13,
    marginTop: 10,
    maxWidth: '70%',
    // textAlign: 'center',
    lineHeight: 23,

  },
  title: {
    color: '#fff',
    fontSize: 14,
    fontWeight: 'bold',
    marginTop: 20,
    // textAlign: 'center',

  },
  image: {
    height: '100%',
    width: '100%',
    resizeMode: 'cover',
  },
  indicator: {
    height: 2.5,
    width: 10,
    backgroundColor: 'grey',
    marginHorizontal: 3,
    borderRadius: 2,
  },
  btn: {
    flex: 1,
    height: 50,
    borderRadius: 5,
    backgroundColor: '#fff',
    justifyContent: 'center',
    alignItems: 'center',
  },
});
export default OnboardingScreen;
